// 分類選單
.product_navbar_form {
  display: flex; }

.product_navbar_item {
  width: 34%; }

.product_navbar_radio {
  display: none;
  &:checked+.product_navbar_label {
    border-bottom-color: $color-red;
    transition-duration: .3s; } }

.product_navbar_label {
  @include font(32px, 1.56);
  display: flex;
  justify-content: center;
  padding: 21px 0 13px;
  border-left: solid 1px $color-gray-light;
  border-right: solid 1px $color-gray-light;
  border-bottom: solid 8px transparent;
  transition-duration: .3s;

  &:hover {
    border-bottom-color: $color-red;
    cursor: pointer;
    transition-duration: .3s; }

  @include ipad {
    @include font(24px, 2.08);
    padding-top: 14px;
    padding-bottom: 6px; }
  @include mobile {
    @include font(14px, 1.64);
    padding-top: 8px;
    padding-bottom: 1px; } }

.product_navbar_icon {
  @include box-size(44px, 44px);
  display: block;

  @include ipad {
    @include box-size(32px, 32px); }
  @include mobile {
    @include box-size(16px, 16px); } }


// banner
.product_banners_lists {
  font-size: 0; }

.product_banner {
  width: 50%;
  height: 304px;
  display: inline-block;
  position: relative;
  font-size: 1px;
  overflow: hidden;
  @include mobile {
    height: 184px; } }

.product_banner_img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  @include ipad {
    width: 150%; }
  @include mobile {
    width: 150%; } }

// 商品主區塊_OPTICAL
.prodocuts_page_content {
  padding: 80px 0 160px;
  h1 {
    @include font(42px, 1.5);
    text-align: center; }
  h3 {
    @include font(32px, 1.5);
    margin-bottom: 40px;
    text-align: center; }
  @include ipad {
    padding-top: 56px;
    padding-bottom: 96px;
    h1 {
      font-size: 32px; }
    h3 {
      font-size: 24px;
      margin-bottom: 32px; } }
  @include mobile {
    padding-top: 32px;
    padding-bottom: 56px;
    h1 {
      @include font(20px, 2.4); }
    h3 {
      @include font(16px, 2.4);
      margin-bottom: 20px; } } }


.prodocuts_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }


.prodocuts_list_item {
  max-width: 24%;
  margin-bottom: 104px;
  @include ipad {
    max-width: 49%;
    margin-bottom: 72px; }
  @include mobile {
    max-width: 100%;
    margin-bottom: 32px; } }

.prodocuts_list_item:nth-child(n+9) {
  margin-bottom: 96px;
  @include ipad {
    margin-bottom: 65px; } }

.prodocuts_list_item:nth-child(n+11) {
  @include ipad {
    display: none; } }

.prodocuts_list_item:nth-child(n+4) {
  @include mobile {
    display: none; } }

.prodocuts_list_item:nth-child(n+3) {
  @include mobile {
    margin-bottom: 40px; } }


.prodocuts_list_img {
  max-width: 100%; }


.prodocuts_list_content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.prodocuts_list_content_title, .prodocuts_list_content_price {
  @include font(24px, 1.5);
  text-transform: uppercase;
  @include ipad {
    line-height: 1.25; }
  @include mobile {
    @include font(16px, 1.875); } }


.prodocut_colors {
  width: 100%; }

.prodocut_color {
  @include box-size(24px, 24px);
  display: inline-block;
  border-radius: 4px;
  @include mobile {
    @include box-size(16px, 16px); } }
.prodocut_color_brown {
  background: $color-brown; }
.prodocut_color_brown_dark {
  background: $color-brown-dark; }


// 商品主區塊_OPTICAL_分頁
.optical_pagination_form {
  max-width: 50%;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  // border: solid 1px red
  @include mobile {
    max-width: 100%; } }

.optical_pagination_item {
  @include ipad {
    flex: 2 0 auto;
    text-align: center; } }


.pagination_label {
  border: solid 1px $color-gray-dark;
  display: block;
  &:hover {
    color: $color-white;
    background: $color-black;
    cursor: pointer; } }

.pagination_link {
  display: none;
  &:checked + .pagination_label {
    color: $color-white;
    background: $color-black; } }

.pagination_link_num {
  @include font(18px, 1.5);
  padding: 9.5px 16px;
  @include ipad {
    padding-left: 0;
    padding-right: 0; }
  @include mobile {
    font-size: 16px; } }

.pagination_link_text {
  @include font(19px, 1.53);
  padding: 8.5px 12px;
  @include ipad {
    padding-left: 0;
    padding-right: 0; }
  @include mobile {
    font-size: 17px; } }

.optical_pagination_item:last-child {
  background: $color-gray-light; }
